import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Chart } from 'chart.js';

/**
 * Generated class for the Skills page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-skills',
  templateUrl: 'skills.html',
})
export class SkillsPage {


  @ViewChild('doughnutCanvas') doughnutCanvas;
  @ViewChild('doughnutCanvas2') doughnutCanvas2;

  doughnutChart: any;
  doughnutChart2: any;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad Skills');

     this.doughnutChart = new Chart(this.doughnutCanvas.nativeElement, {

      type: 'doughnut',
      data: {
        labels: ["Javascript", "TypeScript", "HTML/CSS", "Python"],
        datasets: [{
          label: '# of Votes',
          data: [65, 70, 80, 50],
          backgroundColor: [
            'rgba(231, 243, 119, 1)',
            'rgba(119, 218, 243, 1)',
            'rgba(243, 162, 119, 1)',
            'rgba(119, 164, 243, 1)'
          ],
          hoverBackgroundColor: [
            "#ffff1a",
            "#1a75ff",
            "#ff751a",
            "#1a1aff"
          ]
        }]
      }

    });

    this.doughnutChart2 = new Chart(this.doughnutCanvas2.nativeElement, {

      type: 'doughnut',
      data: {
        labels: ["Ionic", "Angular", "Yii", "Django"],
        datasets: [{
          label: '# of Votes',
          data: [70, 65, 65, 50],
          backgroundColor: [
            'rgba(124, 194, 244, 1)',
            'rgba(244, 124, 124, 1)',
            'rgba(244, 206, 124, 1)',
            'rgba(124, 244, 134, 1)'
          ],
          hoverBackgroundColor: [
            "#1a8cff",
            "#ff3300",
            "#ff9933",
            "#33cc33"
          ]
        }]
      }

    });
  }

}



